/*
  Agency header area
*/
.usa-header--extended .usa-logo {
  max-width: none;
  width: 100%;
  margin: 1rem 0;
}

.agency {
  display: flex;
  align-items: center;

  img {
    width: 4rem;
    height: 100%;
  }

  .agency__name--top {
    font-size: 1.3rem;
    // font-size: 0.8rem;
    font-weight: 500;
    line-height: 1;
  }

  .agency__name--bottom {
    font-size: 0.9rem;
    // font-size: .7rem;
    font-weight: 300;
    text-transform: uppercase;
    margin-top: .3rem;
  }
}
